.toy-details {
	@include content-container();
	width: 90%;
	min-height: 80vh;
	margin: auto;
	flex-direction: column;
	justify-content: space-between;
	font-weight: 700;

	@include from-mobile-layout() {
		width: 50%;
	}

	@include from-wide-layout() {
		width: 40%;
	}

	& > * {
		margin-bottom: 1rem;
	}

	.img-container {
		@include content-container();
		img {
			height: 150px;
		}
	}

	.info {
		@include content-container();
		flex-grow: 1;

		.container {
			display: flex;
			justify-content: center;
			align-items: baseline;
		}
	}

	.back-btn {
		@include app-btn($clr5);
		padding: 1rem;
		width: 20%;
		margin: auto;
		text-align: center;
	}
}
